<template>
	<scroll-view
	 :refresher-triggered="triggered"
	  :refresherrefresh="onRefresh" 
	  :refresher-threshold="80"
	   refresher-background="lightgreen" @refresherpulling="onPulling"
	              @refresherrefresh="onRefresh" @refresherrestore="onRestore" @refresherabort="onAbort"
	 refresher-enabled 
	 enable-back-to-top
	  :scroll-y="true"
	   :scroll-top="scrollTop"
		@scroll="scrollToBottom">
		
		<view class="status_bar">
		            <!-- 这里是状态栏 -->
		        </view>
		<view id="body" class="body m-item" style="display: flex;flex-direction: column;padding-bottom: 100rpx;">
			<view class="background">
				<image mode="widthFix" style="width: 100%;" src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=649482264,581770947&fm=26&gp=0.jpg">

				</image>
			</view>
			<view style="color: #DD524D;width: 750rpx;text-align: center;padding-top: 50px;">{{getStoryText}}</view>
			<!-- message组件 -->
			<view @click="closeMessage(101)">

				<quick-message   ref="message"></quick-message>
				<uni-tag type="primary" inverted circle style="bottom: 200px;left:275rpx; position:fixed;z-index: 1;" text="点我立即关通知"
				 v-if="msgIsOpen" size="normal"></uni-tag>
			</view>
			<view v-for="item in objcs" v-if="objcs">
				<!-- 消息展示区 -->
				<view :class="{mychatBox:item.isMine,otherchatBox:!item.isMine}">

					<view v-if="!item.isMine" class="otherchatTopBox">
						<!-- 他人头像 -->
						<image class="otherAvatarBox" :src="item.avatar"></image>
					</view>

					<view :class="{mychatTopBox:item.isMine,otherChatBottomBox:!item.isMine}">
						<view :class="{mynicknameArea:item.isMine,othernicknameArea:!item.isMine}" style="color: #fffafa;">
							<!-- 昵称 -->
							{{item.nickname}}
						</view>
						<text selectable="" class="contentArea">
							<!-- 消息内容 -->

							{{item.msg}}
						</text>
					</view>

					<view v-if="item.isMine" :class="{myChatBottomBox:item.isMine,otherChatBottomBox:!item.isMine}">
						<!-- 自己头像 -->
						<image :class="{myAvatarBox:item.isMine,otherAvatarBox:!item.isMine}" :src="item.avatar"></image>
					</view>
				</view>
			</view>



			<!-- 锚点 -->
			<!-- <a v-if="objcs" id='ff'></a> -->

			<!-- <a  v-show='false' href='#ff'></a> -->



			<!-- 输入框和发送按钮 -->
			<!-- 手机上下面的bootom要设为0 -->
			<view :class="{phone:platform=='android'
			,h5:platform=='h5',ios:platform=='ios'
			}">
				<view style="width: 80%;">
					<input placeholder="各各都是人才,说话又好听" v-model="content.msg" style="background-color: #FFFFFF;height: 40px;border-radius: 30rpx;margin-left: 5rpx;padding-left: 20rpx;"
					 type="text">

					</input>
				</view>


				<view style="padding-left: 5rpx;">
					<uni-tag :inverted="false" style="width: 100rpx;height: 33px;padding-top: 4px;" @click="sendMsg" circle text="发送"
					 type="primary"></uni-tag>
				</view>

			</view>

			<view :class="{androidShowNumbers:platform=='android',otherShowNumbers:platform!='android'}" style="position: fixed;left: 310rpx;z-index: 1;">
				<text style="width: auto;text-align: center;color: #FFFFFF;">在线人数:{{sysmsg.number}}</text>
			</view>

		</view>

	</scroll-view>

</template>

<script>
	import uniTag from "@/components/uni-tag/uni-tag.vue"
	import message from '@/components/quick-message/quick-message.vue'
	// let userId=''
	// const ws = new WebSocket('ws://localhost:8889'+userId)
	export default {

		components: {
			uniTag,
			message
		},
		data() {
			return {
				messageType: 'success',
				triggered: false,
				_freshing:false,
				getStoryText: '下拉加载历史消息',
				//isShowLoadStoryMsg: true, //是否显示 加载历史消息
				messageTime: 10000,
				scrollTop: 0,
				platform: true,
				screenHeight: '',
				content: {
					userId: '',
					msg: '',
					avatar: '',
					nickname: '',
					address: ''
				},
				getStoryKey: '@!$&^%$#%^&**',
				ifMine: true,
				// 聊天页面时时滚动样式
				style: {
					pageHeight: 0,
					contentViewHeight: 0,
					footViewHeight: 90,
					mitemHeight: 0
				},
				socketTask: null,
				is_open_socket: false,
				objcs: [],
				address: { //
					addr: '', //ip
					country: '',
					province: '',
					city: '',
					isp: '' //运营商
				},
				sysmsg: {
					msg: '',
					number: 0
				},
				userInfo: {

				},
				msgIsOpen: false,
				sockenOpen: false
			}
		},
		mounted() {
			//this.scrollToBottom()
		},
		updated() {
			//this.scrollToBottom()
		},
		onLoad() {
			// 
			console.log('当前用户信息是否存在？',uni.getStorageSync('userInfo'))
			if(!uni.getStorageSync('userInfo')){
			
				setTimeout(()=>{
					this.$refs.message.show({
						msg: '没有登录可不能加入群聊哦，都不知道你是谁',
						mask: true,
						direction:'center',
						time: 1000,
						type: 'error'
					
					})
					uni.switchTab({
						url:'../my/my'
					})
				},1000)
				
			}else{
				this.userInfo=uni.getStorageSync('userInfo')
				this.connectSocketInit()
			} 
			this.getAddress()
			  this._freshing = false;
			            // setTimeout(() => {
			            //     this.triggered = true;
			            // }, 1000)
			
			const res = uni.getSystemInfoSync(); //获取手机可使用窗口高度  api为获取系统信息同步接口
			this.style.pageHeight = res.windowHeight;
			this.style.contentViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (100) - 70; //像素
			
			uni.pageScrollTo({
				duration: 1000,
				scrollTop: this.style.contentViewHeight
			})
			if(uni.getStorageSync('userInfo')){
				
			}
			
			if (res.platform === 'android' || res.platform === 'ios') {
				this.platform = res.platform
			} else {
				this.platform = 'h5'
			}

			this.objcs = uni.getStorageSync('msgs')

		},
		onHide() {
			
			
		},

		onShow() {
			this.scrollToBottom()
			console.log('当前通道是否打开？ ',this.is_open_socket)
			// if(this.is_open_socket===false){
			// 	setTimeout(()=>{
			// 		this.connectSocketInit()
			// 	},1000)
			// }
			
			if(!uni.getStorageSync('userInfo')){
				
				setTimeout(()=>{
					uni.showToast({
						icon:'loading',
						title:'请先登录',
						duration:500
					});
					setTimeout(()=>{
						
						
						uni.switchTab({
							url:'../my/my'
						})
					},1);
				},1000)
				
				
			}else{
				//用户已登录
				this.userInfo= uni.getStorageSync('userInfo')
				if(!this.is_open_socket){
					
					if(uni.getStorageSync('userInfo')){
						
						uni.showToast({
							icon:'loading',
							title:'正在连接'
						})
						setTimeout(()=>{
							//this.connectSocketInit()
						},1000)
					}
					
					//this.is_open_socket=true
				}
			} 
			if(!uni.getStorageSync('msgs')){
				//刚退出的
				this.objcs=[]
				
				
			}else if(this.objcs.length>0){
				//没退出并且拉取过历史记录重新刷新信息
				this.socketTask.send({
					data:this.getStoryKey,
					async success()  {
						console.log('获取历史消息')
					}
				})
			}
			
			console.log(uni.getStorageSync('msgs'))

		},

		beforeDestroy() {
			this.closeSocket();
		},
		onUnload() {

		},
		//////////////////
		methods: {
			//-------------
			// 滚动至聊天底部
			scrollToBottom: function() {
				console.log('滚动至聊天底部')
				setTimeout(() => {
					uni.pageScrollTo({
						scrollTop: 99999,
						duration: 500
					});
				}, 1);


			},
			async sendMsg() {
				console.log('点击了发送按钮')
				this.clickRequest()
				this.scrollToBottom()

			},
			//获取地址  
			getAddress() {
				const val = uni.getStorageSync('address')
				if (val) {
					uni.request({
						url: 'https://app.bilibili.com/x/resource/ip',
						success: (res) => {
							this.address = res.data.data
							console.log(res.data)
							uni.setStorageSync('address', res.data)
						}
					})
				} else this.address.addr = '127.0.0.1'

			},
			getStoryMsg(code, data) {
				if (code === 1) {
					let list = data
					for (var i = 0; i < list.length; i++) {
						let obj = {
							isMine: false,
							avatar: '',
							msg: '',
							nickname: ''
						}
						if (list[i].userId == this.userInfo.id) {

							obj.isMine = true
						}
						obj.avatar = list[i].avatar
						obj.msg = list[i].msg
						obj.nickname = list[i].nickname
						list[i] = obj
					}
					this.objcs = list
				}

			},
			connectSocketInit() {
				// 创建一个this.socketTask对象【发送、接收、关闭socket都由这个对象操作】

				this.socketTask = uni.connectSocket({
					// 【非常重要】必须确保你的服务器是成功的,如果是手机测试千万别使用ws://127.0.0.1:9099【特别容易犯的错误】
					url: this.wsurl + '/chat/' + this.userInfo.nickname,
					success(data) {
						console.log("websocket连接成功");

					},
				});


				// 消息的发送和接收必须在正常连接打开中,才能发送或接收【否则会失败】
				this.socketTask.onOpen((res) => {
					console.log("WebSocket连接正常打开中...！");
					this.is_open_socket = true;
					this.socketTask.onMessage((res) => {


						const data = JSON.parse(res.data)
						console.log("收到服务器内容：");
						console.log(data);
						if (data.number) {
							console.log('收到系统通知')
							// uni.showToast({
							// 	title:data.msg
							// })
							if (data.type == '下线') {
								this.messageType = 'error'
							} else {
								this.messageType = 'success'
							}
							this.msgIsOpen = true
							setTimeout(() => {
								this.msgIsOpen = false;
							}, this.messageTime);
							this.$refs.message.show({
								msg: data.msg,
								mask: false,
								direction:'top',
								time: this.messageTime,
								type: this.messageType
							})
							this.sysmsg = data
						} else {

							if (!data.length) {
								console.log('收到群消息')
								let content = {
									isMine: false,
									nickname: '',
									msg: '',
									avatar: ''
								}
								if (data.userId == this.userInfo.id) {
									content.isMine = true
								}
								content.nickname = data.nickname
								content.msg = data.msg
								content.avatar = data.avatar
								this.objcs.push(content)
								//将消息存到本地
								uni.setStorageSync('msgs', this.objcs)

							} else { //加载历史消息
								console.log('加载历史消息')
								this.scrollToBottom()
								this.getStoryMsg(1, data)
								//将消息存到本地
								uni.setStorageSync('msgs', this.objcs)

							}


						}
					});

				})
				// 这里仅是事件监听【如果socket关闭了会执行】
				this.socketTask.onClose(() => {
					console.log("已经被关闭了")
					this.is_open_socket = false
					this.sysmsg.number-=1
					if(uni.getStorageSync('userInfo'))
					this.connectSocketInit()
				})

			},

			// 关闭websocket【离开这个页面的时候执行关闭】
			closeSocket() {
				this.socketTask.close({
					success(res) {
						this.is_open_socket = false;
						console.log("关闭成功", res)
					},
					fail(err) {
						console.log("关闭失败", err)
					}
				})
			},
			closeMessage(id) {
				let len = this.$refs.message.msgList.length



				for (var i = 0; i < len; i++) {
					this.$refs.message.closeMessage(0)
					console.log('消息被点击')
				}


				this.msgIsOpen = false;


			},



			clickRequest() {

				//console.log('ip'+this.address.addr)
				let that = this
				if (!this.content.msg.trim().length) {
					uni.showToast({
						title: '请输入内容',
						icon: 'loading'
					})
				}
				if (this.is_open_socket && this.content.msg.trim().length) {
					const val = uni.getStorageSync('address')
					// websocket的服务器的原理是:发送一次消,同时返回一组数据【否则服务器会进去死循环崩溃.】
					if (!val) {
						this.address = {
							addr: '127.0.0.1'
						}
					} else {
						this.address = val
					}
					this.content.address = this.address.addr
					this.content.avatar = this.userInfo.avatar
					this.content.nickname = this.userInfo.nickname
					this.content.userId = this.userInfo.id
					this.socketTask.send({
						data: JSON.stringify(this.content),
						async success() {
							console.log("消息发送成功");
							this.content.msg = ''
						},
					});
					that.content.msg = ''
				}


			},
			onPullDownRefresh() {
				console.log('下拉刷新被触发')
				if (this.is_open_socket) {
					setTimeout(() => {
						this.socketTask.send({
							data: this.getStoryKey,
							async success() {
								console.log("获取历史记录成功");
								//this.isShowLoadStoryMsg=false
								uni.stopPullDownRefresh()
							}
						})
					}, 2000)

				}
			},
			onPulling(e) {
				console.log("onpulling", e);
			},
			onRefresh() {
				console.log('自定义下拉刷新被触发')
				if(this._freshing){
					this.triggered=false
					return 
				} 
				this._freshing = true; 
				if(!this.triggered) this.triggered=true
				if (this.is_open_socket) {
					setTimeout(() => {
						this.socketTask.send({
							data: this.getStoryKey,
							async success() {
								console.log("获取历史记录成功");
								//this.isShowLoadStoryMsg=false
								this.triggered = false
								this._freshing=false
							}
						})
						
					}, 2000)

				}
				//this.triggered = true
			},
			onRestore() {
				this._freshing=false
				this.triggered = false; // 需要重置
				console.log("onRestore");
			},
			onAbort() {
				console.log("onAbort");
			}




			//-------------------
		},

		////////////////
	}
</script>

<style>
	  .status_bar {
	        height: var(--status-bar-height);
	        width: 100%;
	    }
	.androidShowNumbers {
		top: 100px;
	}

	.otherShowNumbers {
		top: 70px;
	}

	.background {
		position: fixed;
		top: 0;
		left: 0;

		width: 100%;
		height: 1300rpx;
		/* 		background-image: url(https://gimg2.baidu.com/image_search/src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fq_70%2Cc_zoom%2Cw_640%2Fimages%2F20180611%2F5550b2529de24a1e91e96e616e2fd424.jpeg&refer=http%3A%2F%2F5b0988e595225.cdn.sohucs.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1612256190&t=6734a6e0dfed2bc6ec16541655d66345);
 */

		z-index: -1;
	}

	.h5 {
		bottom: 50px;
		display: flex;
		flex-direction: row;
		background-color: #9c83ff;
		height: 45px;
		padding-top: 5px;
		position: fixed;
		width: 100%;

	}

	.phone {
		bottom: 0 !important;
		display: flex;
		flex-direction: row;
		background-color: #9c83ff;
		height: 45px;
		padding-top: 5px;
		position: fixed;
		width: 100%;
	}

	.ios {
		bottom: 50px;
		display: flex;
		flex-direction: row;
		background-color: #9c83ff;
		height: 45px;
		padding-top: 5px;
		position: fixed;
		width: 100%;
	}

	.body {
		/* background-color: #afeeee; */
	}

	.mychatBox {
		padding: 5px;
		display: flex;
		flex-direction: row;
		float: right;
		margin-top: 50rpx;
		min-height: 70%;
	}

	.otherchatBox {
		padding: 5px;
		display: flex;
		flex-direction: row;
		margin-top: 50rpx;
	}

	.mychatTopBox {
		display: flex;
		flex-direction: column;
	}

	.otherchatTopBox {
		width: 100rpx;
		padding-top: 20rpx;
	}

	.myChatBottomBox {
		width: 100rpx;
		padding-top: 20rpx;
	}

	.otherChatBottomBox {
		display: flex;
		flex-direction: column;
	}

	.myAvatarBox {
		width: 30px;
		height: 30px;
		border-radius: 20px;
		float: right;
	}

	.otherAvatarBox {
		width: 30px;
		height: 30px;
		border-radius: 20px;
	}

	.contentArea {
		color: #333333;
		padding-top: 20rpx;
		padding-bottom: 10px;
		background-color: #FFFFFF;
		border-radius: 20px;
		text-align: center;
		padding: 5px;
	}

	.mynicknameArea {
		text-align: right;
	}

	.othernicknameArea {
		text-align: left;
	}
</style>
